import React from 'react';
import $ from 'jquery';
import Messages from './message-list';
import Input from './input';
import _map from 'lodash/map';
import io from 'socket.io-client';

import './App.css';

export default class App extends React.Component {
    constructor(props) {
        super(props);
        //Khi to state,
        this.state = {
            messages: [], // danh sch tin nhn
            user: {id: '', name: ''},// ngi dng hin ti, nu rng s hin th form login, c s hin th phng chat
            userOnline:[] // danh sch ngi dng ang online
        }
        this.socket = null;
    }
    //Connetct vi server nodejs, thng qua socket.io
    componentWillMount() {
        console.log(this.state.user)
        this.socket = io('https://chattaap.herokuapp.com/');
        this.socket.on('newMessage', (response) => {this.newMessage(response)}); //lng nghe khi c tin nhn mi
        this.socket.on('loginFail', (response) => {alert('Tn  c ngi s dng')}); //login fail
        this.socket.on('loginSuccess', (response) => {this.setState({user: {id: this.socket.id, name: response}})}); //ng nhp thnh cng 
         this.socket.on('updateUesrList', (response) => {this.setState({userOnline: response})}); //update li danh sch ngi dng online khi c ngi ng nhp hoc ng xut

    }
    //Khi c tin nhn mi, s push tin nhn vo state mesgages, v n s c render ra mn hnh
    newMessage(m) {
        const messages = this.state.messages;
        let ids = _map(messages, 'id');
        let max = Math.max(...ids);
        messages.push({
            id: max+1,
            userId: m.user.id,
            message: m.data,
            userName: m.user.name
        });
        let objMessage = $('.messages');
        if (objMessage[0].scrollHeight - objMessage[0].scrollTop === objMessage[0].clientHeight ) {
            this.setState({messages});
            objMessage.animate({ scrollTop: objMessage.prop('scrollHeight') }, 300); //to hiu ng cun khi c tin nhn mi

        } else {
            this.setState({messages});
            if (m.id === this.state.user) {
                objMessage.animate({ scrollTop: objMessage.prop('scrollHeight') }, 300);
            }
        }
    }
    //Gi event socket newMessage vi d liu l ni dung tin nhn v ngi gi
    sendnewMessage(m) {
        if (m.value) {
            this.socket.emit("newMessage", {data:m.value, user: this.state.user}); //gi event v server
            m.value = ""; 
        }
    }
    //login  nh danh ngi dng
    login() {
        this.socket.emit("login", this.refs.name.value); 
    }

    render () {
        return (
           <div className="app__content">
              <h1>chat box</h1>
          {/* kim tra xem user  tn ti hay cha, nu tn ti th render form chat, cha th render form login */}
              { this.state.user.id && this.state.user.name ? 
                <div className="chat_window">
                    {/* danh sch user online */}
                    <div className="menu">
                        <ul className="user">
                        <span className="user-name">{this.state.user.name}</span>
                            <p>Online</p>
                            {this.state.userOnline.map(item =>
                                <li key={item.id}><span>{item.name}</span></li>
                            )}
                        </ul>
                    </div>
                {/* danh sch message */}
                    <div className="content">
                        <Messages user={this.state.user} messages={this.state.messages} typing={this.state.typing}/>
                        <Input sendMessage={this.sendnewMessage.bind(this)}/>
                    </div>
                </div> 
                :
                  <div className="login_form">{/* form login */}
                      <input type="text" name="name" ref="name"></input>
                      <input type="button" name="" value="Login" onClick={this.login.bind(this)}></input>
                  </div>
              }
            </div>
        )
    }
}



